@import '../tokens.scss';

.Header {
  &__wrapper {
    text-align: center;
  }

  &__inner {
    @include container;
  }

  &__logo {
    img {
      width: $bpk-spacing-xl * 5;
    }
  }

  &__heading {
    margin-bottom: $bpk-spacing-sm;
  }

  &__flare {
    width: 100vw;
    max-width: none;
    height: 20rem;
    margin-left: calc(-50vw + 50%);
    background-color: $bpk-color-sky-blue;
    background-image: url('https://backpack.github.io/hero_155227bb4ce1b6bb859a04151cb1618c.png');
    color: $bpk-color-white;

    @include bpk-breakpoint-mobile {
      height: 10rem;
    }
  }
}
